import { api } from '../../utils/api';
import { auth } from '../../utils/auth';
import { ERROR_STATE } from '../../utils/constants';
import {
  loginFailed,
  loginRequest,
  loginSuccess,
  logoutFailed,
  logoutRequest,
  logoutSuccess,
  recoveryFailed,
  recoveryRequest,
  recoverySendEmailSuccess,
  recoverySendPasswordSuccess,
  recoverySetErrorMessage,
  registerFailed,
  registerRequest,
  registerSuccess,
  userChecked,
  userFailed,
  userSuccess,
  userUpdateInfoFailed,
  userUpdateInfoRequest,
  userUpdateInfoSuccess,
} from '../constants';
import { AppThink, TUserBody } from '../types';

export const getUser = (): AppThink => {
  return (dispatch) => {
    const accessToken: string | null = localStorage.getItem('accessToken');
    const refreshToken = localStorage.getItem('refreshToken');
    const isLogin = localStorage.getItem('login');
    if (accessToken && refreshToken && isLogin) {
      api
        .getCurrentUser(accessToken)
        .then((res) => {
          if (res && res.success) {
            dispatch(userSuccess(res.user));
            dispatch(userChecked());
          } else {
            dispatch(userFailed());
          }
        })
        .catch((err) => {
          console.error(err.message);
          switch (err.message) {
            case ERROR_STATE.jwtMalformed || ERROR_STATE.invalidToken: {
              return dispatch(userChecked());
            }
            default: {
              dispatch(userChecked());
              dispatch(userFailed());
              break;
            }
          }
        });
    } else {
      dispatch(userChecked());
    }
  };
};

export const login = (body: TUserBody): AppThink => {
  return (dispatch) => {
    dispatch(loginRequest());

    auth
      .signUp(body)
      .then((res) => {
        if (res && res.success) {
          const accessToken = res.accessToken.split('Bearer ')[1];
          localStorage.setItem('refreshToken', res.refreshToken);
          localStorage.setItem('accessToken', accessToken);
          localStorage.setItem('login', 'true');
          dispatch(loginSuccess(res.user));
        } else {
          dispatch(loginFailed(' '));
        }
      })
      .catch((err) => {
        console.error(err.message);
        switch (err.message) {
          case ERROR_STATE.incorrectData: {
            return dispatch(loginFailed('E-mail    .'));
          }
          default: {
            return dispatch(loginFailed(' '));
          }
        }
      });
  };
};

export const register = (body: TUserBody): AppThink => {
  return (dispatch) => {
    dispatch(registerRequest());

    auth
      .signIn(body)
      .then((res) => {
        if (res && res.success) {
          dispatch(registerSuccess(res.user));
          const accessToken = res.accessToken.split('Bearer ')[1];
          localStorage.setItem('refreshToken', res.refreshToken);
          localStorage.setItem('accessToken', accessToken);
          localStorage.setItem('login', 'true');
        } else {
          dispatch(registerFailed(' '));
        }
      })
      .catch((err) => {
        console.error(err.message);
        switch (err.message) {
          case ERROR_STATE.userExists: {
            return dispatch(
              registerFailed('   .')
            );
          }
          case ERROR_STATE.requiredFields: {
            return dispatch(registerFailed('  .'));
          }
          default: {
            return dispatch(registerFailed(' '));
          }
        }
      });
  };
};

export const logout = (): AppThink => {
  return (dispatch) => {
    dispatch(logoutRequest());
    const refreshToken: string | null = localStorage.getItem('refreshToken');
    if (!refreshToken) throw new Error(' ');
    auth
      .signOut(refreshToken)
      .then((res) => {
        if (res && res.success) {
          dispatch(logoutSuccess());
          localStorage.clear();
        } else {
          dispatch(logoutFailed());
        }
      })
      .catch((err) => {
        console.error(err.message);
        dispatch(logoutFailed());
      });
  };
};

export const updateUserInfo = (body: TUserBody): AppThink => {
  return (dispatch) => {
    dispatch(userUpdateInfoRequest());

    const accessToken: string | null = localStorage.getItem('accessToken');
    if (!accessToken) throw new Error(' ');
    api
      .editUser(body, accessToken)
      .then((res) => {
        if (res && res.success) {
          dispatch(userUpdateInfoSuccess(res.user));
        } else {
          dispatch(userUpdateInfoFailed());
        }
      })
      .catch((err) => {
        console.error(err.message);
        switch (err.message) {
          case ERROR_STATE.jwtMalformed || ERROR_STATE.invalidToken: {
            return dispatch(userChecked());
          }
          default: {
            dispatch(userChecked());
            dispatch(userFailed());
            dispatch(userUpdateInfoFailed());
            break;
          }
        }
      });
  };
};

export const recoveryEmailSend = (body: TUserBody): AppThink => {
  return (dispatch) => {
    dispatch(recoveryRequest());
    auth
      .forgotPassword(body)
      .then((res) => {
        if (res && res.success) {
          dispatch(recoverySendEmailSuccess());
        } else {
          dispatch(recoveryFailed());
          dispatch(recoverySetErrorMessage(',   .'));
        }
      })
      .catch((err) => {
        console.error(err.message);
        dispatch(recoveryFailed());
        dispatch(recoverySetErrorMessage(',   .'));
      });
  };
};

export const recoveryPasswordSend = (body: TUserBody): AppThink => {
  return (dispatch) => {
    dispatch(recoveryRequest());
    auth
      .resetPassword(body)
      .then((res) => {
        if (res && res.success) {
          dispatch(recoverySendPasswordSuccess());
        } else {
          dispatch(recoveryFailed());
          dispatch(recoverySetErrorMessage(',   .'));
        }
      })
      .catch((err) => {
        console.error(err.message);
        dispatch(recoveryFailed());
        switch (err.message) {
          case ERROR_STATE.invalidCredentials: {
            return dispatch(recoverySetErrorMessage(' .'));
          }
          case ERROR_STATE.incorrectToken: {
            return dispatch(recoverySetErrorMessage('   .'));
          }
          default:
            return dispatch(
              recoverySetErrorMessage(',   .')
            );
        }
      });
  };
};
